import React, { useEffect, useState } from "react";
import {NavLink} from "react-router-dom";

import "./index.css";

let navClick = null;

function Head () {
    const [navId, setNavId] = useState("");
    useEffect( () => {
        navClick = function (type) {
            if(navId === type) setNavId("");
            else setNavId(type);
        }
    }, [navId] )

    return <div className="head">
        <div className="head_content">
            <div className="logo">个人中心</div>
            <ul className="nav">
                <li className="case" onClick={ () => { navClick("case") } }>
                    <span className="case_text">案例</span>
                    { navIdJudej("case", navId) && <div className="case_down down" >
                        <NavLink to="/pC/case/project">project</NavLink>                
                        <NavLink to="/pC/case/game">game</NavLink>                
                        <NavLink to="/pC/case/demo">demo</NavLink>                
                    </div> }
                </li>
                <li className="note" onClick={ () => { navClick("note") } }>
                    <span className="note_text">笔记</span>
                    { navIdJudej("note", navId) && <div className="note_down down" >
                        <a href="#">功能开发中</a>                  
                    </div> }
                </li>
                <li className="bok" onClick={ () => { navClick("bok") } }>
                    <span className="bok_text">本人博客</span>
                    { navIdJudej("bok", navId) && <div className="bok_down down" >
                        <a href="/bok">点击进入博客</a>                  
                    </div> }
                </li>
            </ul>
        </div>
    </div>
}

function navIdJudej(boo, navId) {
    if(boo === navId) return true;
    else return false;
}


export default Head;