import React, { useEffect, useState } from "react";
import "./index.css";

import {ajax, url} from "../../tool";

function Case(props) {
    const [caseDom, setCaseDom] = useState([]);
    useEffect( () => {
        ajax(url + "/pC/getCase?type=" + props.match.params.id)
        .then( (d) => {
            const data = JSON.parse(d).data;
            setCaseDom( caseFor(data) );
        } )
    }, [props.match.params.id] )
    return <div className="case_ass">
        <ul className="case_content">
            { caseDom }
        </ul>
    </div>
}

function caseFor(arr) {
    return arr.map( (item) => {
        return <li key={item.id}>
            <a href={url + item.href}>
                <img src={item.imgSrc} />
                <div>
                    <h4>{item.title}</h4>
                    <p>{item.intreduce}</p>
                </div>
            </a>
        </li>
    } )
}



export default Case;